const { SuiClient, getFullnodeUrl } = require("@mysten/sui/client");
const { normalizeSuiAddress } = require("@mysten/sui/utils");
const { SALEBOOK } = require("../constants/contract");

const sui = new SuiClient({
  url: getFullnodeUrl("testnet"),
});

async function useAdminCap(userAddress, saleId) {
  try {
    // 1. Fetch SalesBook object
    const salesBookResp = await sui.getObject({
      id: SALEBOOK,
      options: { showContent: true },
    });

    const salesBook = salesBookResp.data;

    if (!salesBook || salesBook.content?.dataType !== "moveObject") {
      throw new Error("SalesBook object not found or invalid.");
    }

    const outerTableId = salesBook.content.fields.sales;
    const normalizedUser = normalizeSuiAddress(userAddress);

    // 2. Fetch inner table object for user
    const innerTableResp = await sui.getDynamicFieldObject({
      parentId: outerTableId,
      name: {
        type: "address",
        value: normalizedUser,
      },
    });

    const innerTableObj = innerTableResp.data;
    if (!innerTableObj || innerTableObj.content?.dataType !== "moveObject") {
      throw new Error("No inner table found for user.");
    }

    const innerTableId = innerTableObj.content.fields.value;

    // 3. Fetch adminCap from inner table using saleId
    const adminCapResp = await sui.getDynamicFieldObject({
      parentId: innerTableId,
      name: {
        type: "0x2::object::ID",
        value: saleId,
      },
    });

    const adminCapObj = adminCapResp.data;
    if (!adminCapObj || adminCapObj.content?.dataType !== "moveObject") {
      throw new Error("Sale ID not found in inner table.");
    }

    const adminCapId = adminCapObj.content.fields.value;
    return adminCapId;
  } catch (err) {
    console.error(" fetchAdminCapForSale error:", err.message || err);
    return null;
  }
}

module.exports = { useAdminCap };
